@charset "UTF-8";
html, body {
  height: 100%;
  font-size: 10px; }

body {
  background: #ccc;
  font-size: .2rem; }

a:active {
  color: #000; }

.wrapper {
  display: -webkit-box;
  /* 老版本语法: Safari, iOS, Android browser, older WebKit browsers. */
  display: -moz-box;
  /* 老版本语法: Firefox (buggy) */
  display: -ms-flexbox;
  /* 混合版本语法: IE 10 */
  display: -webkit-flex;
  /* 新版本语法: Chrome 21+ */
  display: flex;
  /* 新版本语法: Opera 12.1, Firefox 22+ */
  -webkit-box-direction: normal;
  -webkit-box-orient: vertical;
  -moz-flex-direction: column;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: stretch;
  -moz-align-items: stretch;
  -webkit-align-items: stretch;
  align-items: stretch;
  width: 100%;
  height: 100%; }

header {
  font-size: .15rem; }

.content {
  -webkit-box-flex: 1.0;
  -moz-flex-grow: 1;
  -webkit-flex-grow: 1;
  flex-grow: 1; }
  .content input {
    background-color: #fff; }

footer {
  height: .3rem;
  line-height: .3rem;
  text-align: center; }
